<template lang="pug">
  section.options-board
    p 
    .hr
    div
      input#sale(type="checkbox" v-model="saleOnGameoverCheckbox")
      label(for="sale")    ,   .
    div
      input#log(type="checkbox" v-model="viewLogCheckbox")
      label(for="log")      .
    p(@click="saveAndExit") 
    .hr
    p.info        . 
      |  ,   .
      |    ,        , 
      |            .
      |     alpha -     ,      .
      |         
      a(href="mailto:pomatsuev@gmail.com") pomatsuev()gmail.com
    p.info    ,      ,
      |        ,     ,   
      |   ,    .      .
      |       ,     ...       ,
      |   .
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex'
export default {
  name: 'OptionsBoard',
  computed: {
    ...mapState(['saleOnGameOver', 'viewLog']),
    saleOnGameoverCheckbox: {
      get() {
        return this.saleOnGameOver;
      },
      set(value){
        this.switchSaleOnGameOver(value);
      }
    },
    viewLogCheckbox: {
      get(){
        return this.viewLog;
      },
      set(value) {
        this.switchViewLog(value);
      }
    }
  },
  methods:{
    ...mapMutations(['switchSaleOnGameOver','closeOptions', 'switchViewLog']),
    ...mapActions(['saveOptionsToStorage']),
    saveAndExit(){
      this.saveOptionsToStorage();
      this.closeOptions();
    }
  }
}
</script>

<style lang="scss" scoped>
  .options-board{
    font-size: 16pt;
    text-align: center;
    .info {
      font-size: 20pt;
      padding: 0 10px;
    }
    a{
      display: inline;
    }    
  }
</style>